import React from 'react';
import firebase from 'firebase/app';
import 'firebase/functions';
import ClipLoader from 'react-spinners/ClipLoader';
import { FaPen } from 'react-icons/fa';
import { FaTimes } from 'react-icons/fa';
import { FaTrashAlt } from 'react-icons/fa';

class AllUsers extends React.Component {
 constructor(props) {
  super(props);
  this.state = {
   users: [],
   loading: true,
  };
  this.Rows = this.Rows.bind(this);
 }
 componentDidMount() {
  firebase
   .functions()
   .httpsCallable('allUsers')({
    all: true,
   })
   .then((result) => {
    this.setState({
     users: result.data,
     loading: false,
    });
   });
 }
 Rows() {
  let row = [];
  for (let [key, value] of Object.entries(this.state.users)) {
   row.push(
    <tr key={key} data-name={value.displayName}>
     <td>{value.displayName}</td>
     <td>{value.email}</td>
     <td>{value.uid}</td>
     <td>{`${!!value.disabled ? 'да' : 'нет'}`}</td>
     <td>
      <FaPen /> <FaTrashAlt />
     </td>
    </tr>,
   );
  }
  return row;
 }

 render() {
  return (
   <div className='Admin-ListAllUsers'>
    <div className='Popup'>
     <h1>
      Редактирование пользователя <span id='post-id'></span>
     </h1>
     <button
      className='Close-Popup'
      onClick={() => {
       document.body.classList.remove('Background');
       document.querySelector('.Popup').classList.remove('PopupOpen');
       document
        .querySelectorAll('.Tags-Buttons')
        .forEach((item) => item.classList.remove('Tags-Buttons-Selected'));
      }}
     >
      <FaTimes />
     </button>

     <label>
      Имя
      <input
       className='Callback-Input Input'
       type='text'
       placeholder='Название статьи *'
       id='title'
      />
     </label>

     <label>
      Картинка:
      <input
       className='Callback-Input Input'
       type='text'
       placeholder='Ссылка на картинку *'
       id='img'
      />
     </label>

     <label>
      Права доступа:
      <input
       className='Callback-Input Input'
       type='text'
       placeholder='Права доступа *'
       id='role'
      />
     </label>
     <button onClick={this.editSubmit} className='Button-Submit'>
      Подтвердить
     </button>
    </div>
    <div className='Popup-Delete'>
     <h1>
      Вы уверены что хотите удалить пользователя{' '}
      <span id='delete-post-id'></span>?
     </h1>
     <button
      className='Close-Popup-Delete'
      onClick={() => {
       document.body.classList.remove('Background');

       document
        .querySelector('.Popup-Delete')
        .classList.remove('PopupDeleteOpen');
      }}
     >
      <FaTimes />
     </button>
     <div className='Buttons-Wrapper'>
      <button
       onClick={() => {
        document.body.classList.remove('Background');
        document
         .querySelector('.Popup-Delete')
         .classList.remove('PopupDeleteOpen');
       }}
       className='Button-Cancel'
      >
       Отменить
      </button>
      <button onClick={this.deleteSubmit} className='Button-Submit'>
       Подтвердить
      </button>
     </div>
    </div>
    {this.state.loading ? (
     <ClipLoader size={70} color={'gray'} loading={this.state.loading} />
    ) : (
     <table>
      <thead>
       <tr>
        <th>Имя:</th>
        <th>E-mail:</th>
        <th>Id:</th>
        <th>Отключен:</th>
        <th>Действия:</th>
       </tr>
      </thead>
      <tbody>
       <this.Rows />
      </tbody>
     </table>
    )}
   </div>
  );
 }
}
export default AllUsers;
